body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: lightgrey;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  /*height: fit-content;*/
  display: grid;
  grid-template-columns: 250px 500px;
  /*flex-direction: row;*/
  /*justify-content: flex-start; !* 控制子元素在主轴上的对齐方式为最左边 *!*/
  background-color: lightgrey;

}

.container {
  display: flex;
  flex-direction: row; /* 将主轴方向设置为横向 */
  margin-top: 15%;

}

.row {
  display: flex;
  /*padding: -50px;*/
  margin-top: -10%;

}

.column {
  flex: 1;
  /*border: 1px solid #ccc; !* 添加边框以便观察布局 *!*/
  width: 600px;
  /*margin-right:  -15%;*/
  /*text-align: left;*/
}

.columnR {
  flex: 1;
  /*border: 1px solid #ccc; !* 添加边框以便观察布局 *!*/
  /*width: 100px;*/
  /*margin-right:  -15%;*/
  /*text-align: left;*/
}

.button-container {
  display: flex;
  flex-direction: row;

}

.circle-button {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: lightslategray;
  opacity: 0.8;
  margin-bottom: 10px;
  margin-right: 20px;
  border: 3px solid black;
  cursor: pointer;
  font-size: 20px;


}

.circle-button:hover {
  background-color: lightgrey;
}

.circle-button:focus {
  outline: none;
}

.contact{
  text-align: start;
  margin-top: 12%;
  margin-left: 3%;
}

.header{
  text-align: start;
  margin-top: 15%;
  /*position: ;*/


}

.container_resume{
  display: grid;
  grid-template-columns: 500px 150px 500px;
  grid-template-rows: 100px 100px 100px;
}

.container_projects{
  display: grid;
  grid-template-columns: 600px 1000px ;
  grid-template-rows: 400px 400px 500px 400px 400px 400px 300px 300px;
}

